import React from 'react';
import Logo from './components/Logo';
import Details from './components/Details';
const Topsection = () => (
<div>
	<div className="header-container">
		<div id={'tldvaluescript'}>
	    </div>	
		<div className="overlay"></div>
		<video playsInline="playsinline" autoPlay="autoplay" muted="muted" loop="loop">
			<source src="https://cdn.vnoc.com/realtydao/videoParticle1.mp4" type="video/mp4"/> 
		</video>
		<div className="container">
			<div className="row d-flex align-items-center justify-content-center">
				<div className="col-md-8 text-center">
					
						<Logo />
					
					<div className="w-100 text-white">					
						<h1 className="header-title">Tokenize your <span>domains</span> and exchange it on the <span>marketplace</span></h1>
						<p className="header-title-text">Our platform creates social utility tokens for your brand or domain and allows you to manage its social crypto economy.</p>
						<Details />
					</div>
				</div>
				<div className="col-md-8" id={'tldvalues'}>	
				
				</div>
			</div>
		</div>
	</div>
</div>
)
 
export default Topsection;

