import React from 'react'

function Logo() {
 	var str = window.Configs.domain;
 	var domain_spliter = str.split(".");
 	var domain = domain_spliter[1];
    if(window.Configs.logo===''){
		return (
					<a className="b-link" href={"https://realtydao.com/dna/"+domain} target="_blank" rel="noopener noreferrer">
						<p className="header-subtitle">{window.Configs.domain}/</p>
					</a>
				);
	}else{
		return (
					<a className="b-link" href={"https://realtydao.com/dna/"+domain} target="_blank" rel="noopener noreferrer">
						<img src={window.Configs.logo} className="img-responsive img-dom" alt=""/>
					</a>
				);
	}
  
}

export default Logo
