import React from 'react'

function Details() {
 	var str = window.Configs.domain;
 	var domain_spliter = str.split(".");
 	var domain = domain_spliter[1];

 	return (
				<div className="learn-more-btn-section"> 
					<a className="btn btn-warning font-weight-bold" href={"https://realtydao.com/dna/"+domain} target="_blank" rel="noopener noreferrer">
						View Details
					</a> 
				</div>
			);
   
  
}

export default Details
